import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import GameSelect from '../Components/GameSelect';
import CreateGameForm from '../Components/CreateGameForm';
import GamePage from '../Pages/GamePage';
import axios from 'axios';

const HomePage: React.FC = () => {
    const [games, setGames] = useState<{ id: number, gameName: string }[]>([]);
    const [selectedGame, setSelectedGame] = useState<string | null>(null);
    const navigate = useNavigate();

    useEffect(() => {
        axios
            .get('http://localhost:5154/api/FizzBuzzRules/games')
            .then((response) => {
                setGames(response.data);
            })
            .catch((error) => console.error('Error fetching games:', error));
    }, []);

    const handleGameSelect = (gameName: string) => {
        setSelectedGame(gameName); // Assuming you select by game name
    };

    const handleBeginGame = () => {
        if (selectedGame) {
            navigate(`/game/${selectedGame}`);
        } else {
            alert('Please select a game first.');
        }
    };

    return (
        <div>
            <h1>Welcome to FizzBuzz</h1>
            <button onClick={handleBeginGame}>Begin</button>
            <GameSelect onSelect={handleGameSelect} />
            <CreateGameForm />
        </div>
    );
};

export default HomePage;
